import React, {Suspense, useState} from "react";
import {Route, Routes} from "react-router-dom";
import classes from './ContentContainer.module.css';
import ErrorBoundary from "../common/ErrorBoundary/ErrorBoundary";
import Tasks from "../Tasks/Tasks";
import ProfileContainer from "../Profile/ProfileContainer";
import Modal from "../common/Modal/Modal";
import Scss from "../Scss/Scss";
import NavBarContainer from "../MyFriends1/MyFriendsContainer";
import Home from "../Home/Home";

//const ProfileContainer = React.lazy(() => import("../Profile/ProfileContainer"))
const DialogsContainer = React.lazy(() => import("../DialogList/DialogListContainer"))
//    ,        ,   lazy
const ExportDefaultUsersContainer = React.lazy(() => import("../users/ExportDefaultUsersContainer"))
const LoginContainer = React.lazy(() => import("../Login/LoginContainer"))
const News = React.lazy(() => import("../News/News"))
const Rest = React.lazy(() => import("../Rest/Krestiki-Noliki/krestiki-noliki"))
const StackInfo = React.lazy(() => import("../Info/StackInfoBS"))
const FeedBackContainer = React.lazy(() => import("../FeedBack/FeedBackContainer"))

let ContentContainer = () => { //      
    const [show, setShow] = useState(false)
    return (<div>
        <ErrorBoundary> {/*   ContentContainer*/}
            <Suspense fallback={
                <div>...</div>}> {/* ,    Lazy import   fallback   */}
                <div className={classes.contentClass}>
                    <Routes> {/*   URL   */}
                        <Route path='' element={<Home/>}/> {/* */}
                        <Route path='/mystack/*' element={<StackInfo/>}/> {/* */}
                        <Route path='/profile/*' element={<ProfileContainer/>}/> {/**/}
                        <Route path='/dialogs/*' element={<DialogsContainer/>}/> {/**/}
                        <Route path='/users/*' element={<ExportDefaultUsersContainer/>}/> {/*  UsersBS*/}
                        <Route path='/friends/*' element={<NavBarContainer/>}/> {/*   content*/}
                        <Route path='/login/*' element={<LoginContainer/>}/> {/**/}
                        <Route path='/news/*' element={<News/>}/> {/*   hn algonia*/}
                        <Route path='/rest/*' element={<Rest/>}/> {/* */}
                        <Route path='feedback' element={<FeedBackContainer/>}/> {/* */}
                        <Route path='tasks' element={<Tasks/>}/> {/* */}
                        <Route path='scss' element={<Scss/>}/> {/* */}
                    </Routes>
                    <div>
                        {/*  <button onClick={()=>{setShow(true)}}>Show ModalBS</button> {/*    */}
                        <Modal
                            show={show} // show /  
                            onClose={()=>{setShow(false)}} //     
                            modal_title={"ModalBS Title"} //   
                            modal_body={"This is modal content"} //   
                        />
                    </div>
                </div>
            </Suspense>
        </ErrorBoundary>
    </div>)
}
export default ContentContainer
