import React, {useCallback, useEffect} from 'react'
import classes from './Modal.module.css'

const Modal = ({show,onClose,modal_title, modal_body}) => {
    const escFunction = useCallback((event)=>{
        if (event.key==="Escape") {
            //    escape
            onClose()
        }
    },[onClose])

    useEffect(()=>{
        document.addEventListener("keydown", escFunction)
        return () => {

            document.addEventListener("keydown", escFunction)
        }
    }, [escFunction])

    if (!show) {
        return null
    }

    return (
        <div className={classes.modal} onClick={onClose}> {/*      */}
            <div className={classes.modal_content}
                 onClick={(e)=>{e.stopPropagation()}}>
                {/*    ,   */}
                <div className={classes.modal_header}>
                    <h4 className={classes.modal_title}>{modal_title}</h4> {/*  */}
                </div>
                <div className={classes.modal_body}>{modal_body}</div> {/*  */}
                <div className={classes.modal_footer}>
                    <button className={classes.button} onClick={onClose}>Close</button>
                    {/*    */}
                </div>
            </div>
        </div>
    )
}

export default Modal
